<template>
    <div id="addGame">
        <p class="title_main"></p>
        <div class="view_main">
            <span></span>
            <el-input class="input_type"></el-input>
            <span></span>
            <el-input class="input_type"></el-input>
            <span></span>
            <el-select v-model="isUse" placeholder="">
                <el-option
                        v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
            <span></span>
            <el-select v-model="isUse" placeholder="">
                <el-option
                        v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
           </el-select>
        </div>
        <div  class="view_main">
            <span></span>
            <el-input
                    class="textarea_type"
                    type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
            placeholder=""
            v-model="textarea">
        </el-input>
        </div>
        <div  class="view_main">
            <span></span>
            <el-select v-model="typeValue1" placeholder="">
                <el-option
                v-for="item in optionsType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div  class="view_main">
            <span></span>
            <el-tag
            v-for="tag in tags"
            :key="tag.name"
            closable
            :type="tag.type">
  {{tag.name}}
            </el-tag>
            <el-button @click="LabelDialog(true)"></el-button>
        </div>
        <div class="view_main">
            <p></p>
            <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
        </div>
            <div class="view_main">
                <ul class="game_info">
                    <li>
                    <p class="padd_type"></p>
                        <p>0</p>
                        </li>
                    <li>
                        <p class="padd_type"></p>
                        <p>0</p>
                    </li>
                    <li>
                        <p class="padd_type"></p>
                        <p>0</p>
                    </li>
                    <li>
                        <p class="padd_type"></p>
                        <p>0</p>
                    </li>
                </ul>
            </div>
            <div class="view_main">
                <el-button></el-button>
            </div>
            <el-dialog title="" :visible.sync="dialogTableVisible">
                <div>sadasd</div>
                <el-button @click="LabelDialog(false)"></el-button>
            </el-dialog>
    </div>
</template>
        <script>
            export default{
                data(){
                    return{
                        isUseMenu: [
                            {value: "true",
                                label: ""},
                            {value: "false",
                                label: ""}
                        ],
                        isUse: "false",
                        textarea:"",
                        optionsType: [
                            {
                                value: '1',
                                label: ''
                            },
                            {
                                value: '2',
                                label: ''
                            },
                            {
                                value: '3',
                                label: ''
                            },
                            {
                                value: '4',
                                label: ''
                            },
                            {
                                value: '5',
                                label: ''
                            }
                        ],
                        typeValue1: '',
                        tags: [
                            { name: '', type: '' },
                            { name: '', type: 'success' },
                            { name: '', type: 'info' },
                            { name: '', type: 'warning' },
                            { name: '', type: 'danger' }
                        ],
                         imageUrl: '',
            dialogTableVisible: false,
                    }
                },
                methods:{
            handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
            this.$message.error(' JPG !');
            }
            if (!isLt2M) {
            this.$message.error(' 2MB!');
            }
            return isJPG && isLt2M;
            },
            LabelDialog(boolean){
            this.dialogTableVisible = boolean;
            }
            }
            }
        </script>
        <style>
        .input_type{
            width: 200px;
        }
        .textarea_type{
            width: 500px;
        }
        .title_main{
            padding:20px 0;
            border-bottom: 1px solid #999;
            }
        .view_main{
            margin-top:10px
            }
        .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            }
        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
            }
        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 178px;
            height: 178px;
            line-height: 178px;
            text-align: center;
            }
        .avatar {
            width: 178px;
            height: 178px;
            display: block;
            }
            .game_info{
                overflow:hidden;
                background:#efefef;
                padding:10px 0;
            }
        .game_info li{
                float:left;
                width:150px;
            height:150px;
                text-align:center;
            background:#666666;
            color:#fff;
            margin-left:20px;
            }
            .padd_type{
            padding-top:30px;
            }
        </style>


