<template>
    <div id="circleType">
        <p class="title_main"></p>
        <div class="view_main">
            <el-button @click="LabelDialog(true)"></el-button>
        </div>
        <div class="view_main">
            <template>
                <el-table
                :data="tableData"
                border
                style="width: 100%">
                    <el-table-column
                    fixed
                    prop="id"
                    label="ID"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="name"
                    label=""
                    >
                    </el-table-column>
                    </el-table-column>
                    <el-table-column
                    label=""
                    >
                        <template slot-scope="scope">
                            <el-button type="text" size="small"></el-button>
                            <el-button type="text" size="small"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>
    <el-dialog title="" :visible.sync="dialogTableVisible" width="30%">
        <div class="view_main">
            <span></span>
            <el-input class="input_type"></el-input>
        </div>
        <el-button @click="LabelDialog(false)"></el-button>
    </el-dialog>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                tableData: [{
                    id: '2016-05-03',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }],
                dialogTableVisible:false
            }
    },
    methods:{
        LabelDialog(boolean){
             this.dialogTableVisible = boolean;
        }
    }
}
</script>
<style>
.title_main{
    padding:20px 0;
    border-bottom: 1px solid #999;
    }
.view_main{
    overflow:hidden;
    margin-top:10px
    }
.input_type{
    width: 200px;
    }
</style>


