<template>
    <div id="courseware">
            <div class="type_menu">
            <el-select v-model="typeValue1" placeholder="">
                <el-option
                        v-for="item in optionsType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-select v-model="isUse" placeholder="">
            <el-option
                    v-for="item in isUseMenu"
            :key="item.value"
            :label="item.label"
            :value="item.value">
        </el-option>
    </el-select>
    <el-input
    placeholder=""
    class="search_input"
    v-model="searchText">
    </el-input>
    <el-button></el-button>
    <el-button></el-button>
            </div>
        <el-button class="add_btn" @click="addEvent"></el-button>
<template>
<el-table
:data="tableData"
border
style="width: 100%">
<el-table-column
        fixed
        prop="id"
        label="ID"
       >
</el-table-column>
<el-table-column
        prop="name"
        label=""
        >
</el-table-column>
<el-table-column
        prop="province"
        label=""
       >
</el-table-column>
<el-table-column
        prop="zip"
        label=""
        >
</el-table-column>
<el-table-column
        prop="city"
        label=""
      >
</el-table-column>
<el-table-column
        prop="address"
        label=""
    >
</el-table-column>
<el-table-column
label=""
>
<template slot-scope="scope">
    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
<el-button type="text" size="small"></el-button>
</template>
        </el-table-column>
        </el-table>
        </template>
        </div>
        </template>
<style>
#courseware {
    text-align: left;
}
.search_input{
    width: 300px;
}
.add_btn{
    margin-top: 10px;
}
</style>
<script>
export default
{
    data()
    {
        return{
            optionsType: [
                {
                    value: '1',
                    label: ''
                },
                {
                    value: '2',
                    label: ''
                },
                {
                    value: '3',
                    label: ''
                },
                {
                    value: '4',
                    label: ''
                },
                {
                    value: '5',
                    label: ''
                }
            ],
            typeValue: '',
            typeValue1: '',
            isUseMenu: [
                {value: "true",
                    label: ""},
                {value: "false",
                    label: ""}
            ],
            isUse: "false",
            searchText:"",
            tableData: [{
                id: '2016-05-03',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }]
        }
    },
    methods:{
        addEvent(){
            this.$router.push("/addCourseware");
        }
    }

}
</script>


