<template>
    <div id="addCourseware">
        <p class="title_main"></p>
        <div class="view_main">
            <span></span>
            <el-input v-model="coursewarename" class="input_type" />
            <span></span>
            <el-select v-model="value" placeholder="">
                <el-option
                        v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>

        <span></span>
        <el-select v-model="isdisable" placeholder="">
            <el-option
               v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        </div>
        <div class="view_main">
            <span></span>
            <el-input class="input_type" />
            <span></span>
            <el-select v-model="kejianName" placeholder="">
                <el-option
                v-for="item in kejianNameOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="view_main">
            <span></span>
            <el-input class="input_type" />
            <el-button></el-button>
            <el-button></el-button>
        </div>
        <div class="view_main">
            <el-button></el-button>
        </div>
    </div>
</template>
        <script>
        export default {
            data(){
                return{
                    coursewarename:"",
                    options: [{
                        value: '1',
                        label: ''
                    }, {
                        value: '2',
                        label: ''
                    }, {
                        value: '3',
                        label: ''
                    }, {
                        value: '4',
                        label: ''
                    }, {
                        value: '5',
                        label: ''
                    }],
                    value: '',
                    options: [{
                        value: 'true',
                        label: ''
                    }, {
                        value: 'false',
                        label: ''
                    }],
                    isdisable:"false",
                    kejianNameOptions: [{
                        value: 'true',
                        label: ''
                    }, {
                        value: 'false',
                        label: ''
                    }],
                    kejianName:"false"
                }
            }
        }
        </script>
        <style>
        .title_main{
            padding:20px 0;
            border-bottom: 1px solid #999;
            }
        #addCourseware{
            text-align: left;
        }
            .input_type{
                width: 200px;
            }
        .view_main{
            margin-top:10px
            }
        </style>


